import Vue from 'vue'
import Router from 'vue-router'
import Entry from '../components/Entry'

Vue.use(Router)

export default new Router({
    routes: [
        {
            // query params include [appId, appVersion, appConfigId, roomId, userId, userRole, mode, keynotePageId, firstStrokePageId, withBiz]
            // mode 值可以为 livecast、playback、dev，表示直播模式、回放模式、开发模式
            // withBiz 用来区分加载时是否会通过biz同步状态，用以区分首次和后续加载
            path: '/',
            name: 'entry',
            component: Entry
        }
    ]
})



